<template>
  <div class="list">
    <div class="clearfix list-button">
      <div class="pull-left">
        <el-button   @click="createNew">创建新商机</el-button>
        <el-menu @select="searchShow(item.value)" class="list-state-demo clearfix" :default-active="stateValue">
          <div v-for="(item,index) in stateOptions" :key="index" class="list-state-menu">
            <el-menu-item :index="item.value" :key="item.value">
              <span >{{ item.label }}</span>
            </el-menu-item>
            <div class="list-state-line" v-if="!(index===4)">|</div>
          </div>
        </el-menu>
      </div>
    </div>
    <div class="list-center">
      <!--列表-->
      <el-table :data="tableData"  v-loading="listLoading"
                @row-click="lookDetail"  class="chanceTable">
        <el-table-column prop="id" label="编号" width="60" height="40" align="center">
        </el-table-column>
        <el-table-column prop="name" label="项目名称" align="center" >
        </el-table-column>
        <el-table-column prop="contact" label="联系人" align="center"  >
        </el-table-column>
        <el-table-column prop="contactPhone" label="联系人手机号"  align="center"  >
        </el-table-column>
        <el-table-column prop="chanceTime" label="商机登记时间" align="center"  sortable>
        </el-table-column>
        <el-table-column prop="channel" label="渠道"  align="center" >
        </el-table-column>
        <el-table-column prop="person" label="销售负责人"  align="center" >
        </el-table-column>
        <el-table-column prop="state" label="状态"  align="center" >
        </el-table-column>
      </el-table>
      <!--工具条-->
      <el-col :span="24" class="toolbar">
        <el-pagination background layout="prev, pager, next,total"
                       @current-change="pageChange" :page-size="10" :total="total">
        </el-pagination>
      </el-col>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'chance',
    components: {},
    data () {
      return {
        stateOptions: [{
          value: 'state1',
          label: '新线索'
        }, {
          value: 'state2',
          label: '跟进中'
        }, {
          value: 'state3',
          label: '已成单'
        }, {
          value: 'state4',
          label: '已终止'
        }, {
          value: 'state5',
          label: '全部'
        }],
        stateValue: 'state5',
        total: 7,
        params: {
          page: 1,
          limit: 10,
          category: ''
        },
        listLoading: false,
        tableData: [],
        listData: [{
          id: 1,
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          channel: '今日头条',
          person: '张三',
          state: '新线索'
        }, {
          id: 2,
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          channel: '橙悦小程序',
          person: '李四',
          state: '跟进中'
        }, {
          id: 3,
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          channel: '橙悦官网PC端',
          person: '王二麻子',
          state: '已终止'
        }, {
          id: 4,
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          channel: '橙悦官网手机端',
          person: '张三',
          state: '全部'
        }, {
          id: 5,
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          channel: '橙悦服务号图文',
          person: '李四',
          state: '新线索'
        }, {
          id: 6,
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          channel: '丁香园',
          person: '王二麻子',
          state: '已终止'
        }, {
          id: 7,
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          channel: '解放号',
          person: '张三',
          state: '全部'
        }]
      }
    },
    watch: {
      /**
       * @params 监听参数变化重新获取数据
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      createNew () {
//        this.$store.commit('LOOK_CHANCE', {})
        this.$router.push({
          path: '/crm/chanceDetail',
          query: {
            type: 'create'
          }
        })
      },
      /**
       * @params:category 分类 page：页码 limit:条数
       * */
      getData (params) {
        this.tableData = this.listData
      },
      /**
       * @on-change 页码改变的回调，返回改变后的页码
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * 表格对应操作方法
       * @searchShow 搜索
       * @handleDel 删除
       * @edit
       */
      searchShow (argumentObj) {
        this.params.category = argumentObj
      },
      // 删除
      handleDel: function (index, row) {
      },
      edit (index) {
      },
      /**
       * 查看
       */
      lookDetail (row) {
//        this.$store.commit('LOOK_CHANCE', row)
        this.$router.push({
          path: '/crm/chanceDetail',
          query: {
            type: 'detail',
            id: row.id
          }
        })
      }
    },
    created () {
      this.getData(this.params)
    },
    mounted () {
    }
  }
</script>



